import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	components: {
		uniIcon
	},
	name: 'Bugbtn',
	data () {
		return {
			num: 0
		};
	},
	props: {
		max: {
			default: 10
		}
	},
	methods: {
		clkMinus () {
			this.num > 0 && (this.num -= 1);
			this.$emit('click', {
				'num': this.num
			});
		},
		clkPlus () {
			if (this.num < this.max) {
				this.num += 1;
				this.$emit('click', {
					'num': this.num
				});
			} else {
				this.$emit('click', {
					'error': '已达上限'
				});
			}
		}
	}
}
